@font-face {
  font-family: 'New York';
  src: url(./NewYork.otf);
}
@font-face {
  font-family: 'Bubasaur';
  src: url(./Bubasaur.ttf);
}
@import url('https://fonts.googleapis.com/css2?family=Erica+One&display=swap');
body
{
  background-image:  
  radial-gradient(at 0% 4%, hsla(169,100%,89%,1) 0, transparent 58%),  
  radial-gradient(at 91% 0%, hsla(169,100%,95%,1) 0, transparent 58%),  
  radial-gradient(at 1% 98%, hsla(194,100%,84%,1) 0, transparent 70%),  
  radial-gradient(at 60% 42%, hsla(194,58%,78%,1) 0, transparent 85%),  
  radial-gradient(at 79% 98%, hsla(93,100%,92%,1) 0, transparent 53%);
  background-position: center;
  background-size: cover;
  --webkit-background-size: cover;
}
.App {
  padding: 1em;
  text-align: center;
  width: 90%;
  margin: 0 auto;
  user-select: none;
}

h1,h2,h3,h4,h5,a,p
{
  font-family: 'bubasaur', cursive;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
nav
{
  background-color: rgb(230,235,255);
  height: 150px;
  display: grid;
  place-items: center;
  font-size: 2em;
  border-radius: 1em;
  box-shadow:
  0px 0px 3.2px -25px rgba(0, 0, 0, 0.046),
  0px 0px 7.1px -25px rgba(0, 0, 0, 0.066),
  0px 0px 11.9px -25px rgba(0, 0, 0, 0.078),
  0px 0px 18px -25px rgba(0, 0, 0, 0.085),
  0px 0px 25.9px -25px rgba(0, 0, 0, 0.09),
  0px 0px 36.7px -25px rgba(0, 0, 0, 0.093),
  0px 0px 52px -25px rgba(0, 0, 0, 0.096),
  0px 0px 75.6px -25px rgba(0, 0, 0, 0.1),
  0px 0px 116.4px -25px rgba(0, 0, 0, 0.107),
  0px 0px 207px -25px rgba(0, 0, 0, 0.13)
;

}
a:hover
{
  color: #ffffff;
  text-decoration: none;
}
a,table
{
  color: rgb(112,208,236) !important;
}
.statistics
{
  margin-top: 2em;
  border-radius: 2em;align-items: center !important;
  box-shadow:
  0px 0px 3.2px -25px rgba(0, 0, 0, 0.046),
  0px 0px 7.1px -25px rgba(0, 0, 0, 0.066),
  0px 0px 11.9px -25px rgba(0, 0, 0, 0.078),
  0px 0px 18px -25px rgba(0, 0, 0, 0.085),
  0px 0px 25.9px -25px rgba(0, 0, 0, 0.09),
  0px 0px 36.7px -25px rgba(0, 0, 0, 0.093),
  0px 0px 52px -25px rgba(0, 0, 0, 0.096),
  0px 0px 75.6px -25px rgba(0, 0, 0, 0.1),
  0px 0px 116.4px -25px rgba(0, 0, 0, 0.107),
  0px 0px 207px -25px rgba(0, 0, 0, 0.13)
;

}
.statistics-text
{
  background-color: rgb(230,235,255);
  display: grid;
  border-radius: 2em !important;
  grid-template-columns: 1fr 1fr;
  place-items: center;
  text-align: left;
  height: 500px;
}
.jumbotron
{
  padding: 32px 32px !important;
}
h1,h2,h4,h4,h6
{
  font-size: 4em !important;
  color: rgb(112,208,236);
}
p
{
