.showcase
{
  height: 500px !important;
  overflow: hidden;
  position: absolute;
  width: 50%;
  background-image: url(./components/images/pygras/PygraShowcase.png);
  animation: showcase 12s linear infinite !important;
  background-size: 100% !important;
  background-repeat: repeat-y !important;
  right: 6em;
}

@keyframes
showcase
{
  0% {background-position: 50% 0;}
    100% {background-position: 50% -1900px;}
}


